*,:after,:before{-ms-box-sizing:border-box;box-sizing:border-box}html{color:#444;direction:ltr;font-size:1em;line-height:1.4}html,html a{text-rendering:optimizeLegibility!important;text-shadow:1px 1px 1px rgba(0,0,0,.004)}h1,h2,h3{color:#1b1b1b;font-weight:400}p{color:#555}a:focus,a:hover{outline:0;text-decoration:none}hr{border:0;border-top:1px solid #ddd;height:1px;margin:1em 0;padding:0}button{font-size:16px;padding:10px 20px}textarea{height:200px;padding-bottom:15px;width:100%}input,textarea{border:1px solid #ccc;color:#888;font-size:1em;padding:.6em 10px .6em 8px;border-radius:4px;-webkit-transition:border .3s ease-in-out;transition:border .2s ease-in-out;outline:none}input:focus,textarea:focus{border-color:#aaa;box-shadow:none;outline:0;-webkit-box-shadow:none}label{color:#333;display:block;font-weight:400;margin-bottom:.25em;text-transform:none}:focus{outline:0 dotted #f36}@media print{a[href]:after{content:none!important}}